.about {
  padding-bottom: 40px;
  background-color: #192144;
  background-image: url("../img/about-mobile@1x.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
  border-radius: 0 0 30px 30px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/about-mobile@2x.png");
  }
}

.about__wrapper {
  padding-top: 1px;
  width: 270px;
  margin: 0 auto;
  padding-bottom: 20px;
}

.about .button {
  display: none;
}

.about__title {
  padding-left: 24px;
  margin-bottom: 18px;

  @include font-parametrs(24px, $orange-title, 700, 24px);
}

.about__text {
  padding: 0;
  margin: 0;
  width: 210px;

  @include font-parametrs(16px, $white, 300, 22px);
  padding-left: 24px;
  padding-right: 35px;
  margin-bottom: 20px;
}

.about__text--call {
  font-weight: 700;
  margin-bottom: 0;
}

@media (min-width: $tablet-width) {
  .about {
    background-image: none;
    position: relative;
    z-index: 8;
  }

  .about__wrapper {
    width: 688px;
    margin: 0 auto;
    margin-top: -20px;
    padding-top: 20px;
  }

  .about .button {
    display: block;
    margin-left: 35px;
  }

  .about__title {
    padding-left: 34px;
    padding-top: 42px;
    font-size: 45px;
    margin-bottom: 38px;
    line-height: 45px;
    position: relative;
    z-index: 1;

    &::before {
      position: absolute;
      content: "";
      width: 688px;
      height: 213px;
      border-radius: 20px;
      background-color: #141b3b;
      top: 0;
      left: 0;
      z-index: -1;
    }

    &::after {
      position: absolute;
      content: "";
      background-image: url("../img/about-tablet@1x.png");
      width: 376px;
      height: 548px;
      left: 344px;
      top: 48px;
      z-index: -1;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/about-tablet@2x.png");
        background-size: contain;
      }
    }
  }

  .about__text {
    font-size: 28px;
    line-height: 33px;
    padding-left: 34px;
    padding-right: 0;
    width: 300px;
    margin-bottom: 33px;
    position: relative;
    z-index: 1;
  }

  .about__text--call {
    font-size: 21px;
    line-height: 25px;
    margin-bottom: 51px;
  }
}

@media (min-width: $desktop-width) {
  .about {
    background-color: #192144;
    margin-top: -105px;
    z-index: 0;
  }

  .about__wrapper {
    padding-top: 67px;
    width: 1320px;
    margin: 0 auto;
    padding-bottom: 94px;
  }

  .about .button {
    margin-left: 71px;
  }

  .about__title {
    padding-top: 134px;
    padding-left: 70px;
    margin-bottom: 57px;

    &::after {
      position: absolute;
      content: "";
      background-image: url("../img/about-desktop@1x.png");
      width: 630px;
      height: 665px;
      left: 690px;
      top: 86px;
      z-index: -1;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/about-desktop@2x.png");
        background-size: contain;
      }
    }

    &::before {
      top: 70px;
      width: 213px;
    }
  }

  .about__text {
    padding-left: 71px;
    width: 620px;
    margin-bottom: 39px;
  }

  .about__text--call {
    width: 410px;
    margin-bottom: 84px;
  }
}
