.country {
  background-color: #edeff6;
  position: relative;
  padding-bottom: 19px;
  margin-top: -20px;
  z-index: -1;

  &::before {
    position: absolute;
    content: "";
    top: -150px;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    height: 0;
    background-color: #192144;
    border-radius: 0 0 30px 30px;
  }
}

.country__wrapper {
  width: 270px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.country__title {
  padding-top: 71px;
  padding-left: 25px;

  @include font-parametrs(24px, $orange-title, 700, 24px);
  margin-bottom: 20px;
}

.country__title-description {
  @include font-parametrs(16px, $gray, 300, 22px);
  width: 195px;
  padding-left: 24px;
  margin-bottom: 59px;
}

.country__list {
  @include list-reset;
  margin-bottom: 17px;
}

.country__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  position: relative;

  &:nth-child(2) .country__title-review {
    margin-right: 96px;
  }

  &:nth-child(3) .country__title-review {
    margin-right: 133px;
  }

  &:first-child .country__review {
    position: relative;

    &::after {
      position: absolute;
      content: "";
      background-image: url("../img/flag-thailand.svg");
      background-repeat: no-repeat;
      background-size: 35px, 24px;
      width: 35px;
      height: 24px;
      top: -12px;
      left: 25px;
    }
  }

  &:nth-child(2) .country__review {
    margin-top: -15px;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      background-image: url("../img/flag-france.svg");
      background-repeat: no-repeat;
      background-size: 35px, 24px;
      width: 35px;
      height: 24px;
      top: -12px;
      left: 25px;
    }
  }

  &:nth-child(3) .country__review {
    margin-top: -15px;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      background-image: url("../img/flag-czech.svg");
      background-repeat: no-repeat;
      background-size: 35px, 24px;
      width: 35px;
      height: 24px;
      top: -11px;
      left: 25px;
    }
  }
}

.country__image {
  border-radius: 10px 10px 0 0;
}

.country__review {
  background-color: $white;
  margin-top: -14px;
  border-radius: 10px;
  display: flex;
}

.country__title-review {
  padding-top: 6px;
  padding-left: 24px;

  @include font-parametrs(21px, $navy-blue, 700, 21px);
  margin-right: 101px;
}

.country__text {
  display: block;
  margin-top: -20px;
  padding-left: 24px;
  padding-bottom: 23px;

  @include font-parametrs(16px, $gray, 500, 18px);
  opacity: 0.3;
}

.country__counter {
  @include font-parametrs(16px, $gray, 500, 30px);
  text-transform: uppercase;
  padding-top: 24px;
  opacity: 0.3;
  position: relative;

  &::before {
    opacity: 0.2;
    position: absolute;
    content: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3C!-- Generator: Adobe Illustrator 21.0.0, SVG Export Plug-In . SVG Version: 6.00 Build 0) --%3E%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 20 20' width='11' height='11' style='enable-background:new 0 0 20 20;' xml:space='preserve'%3E%3Cg id='icons'%3E%3Cg id='user'%3E%3Cellipse fill='%23192144' cx='10' cy='6' rx='5' ry='6'/%3E%3Cpath fill='%23192144' d='M19.8,17.1c-0.9-1.8-2.6-3.3-4.8-4.2c-0.6-0.2-1.3-0.2-1.8,0.1c-1,0.6-2,0.9-3.2,0.9S7.8,13.6,6.8,13 c-0.5-0.2-1.2-0.3-1.8,0c-2.2,0.9-3.9,2.4-4.8,4.2C-0.5,18.5,0.6,20,2.1,20h15.8C19.4,20,20.5,18.5,19.8,17.1z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E%0A");
    top: 24px;
    left: -18px;
  }
