.country__button {
  order: 3;
  font-size: 18px;
  line-height: 18px;
  border-radius: 10px;
  width: 270px;
  text-transform: none;
  position: relative;
  z-index: 999;

  span {
    padding-top: 13px;
    padding-bottom: 11px;
    padding-left: 10px;
  }

  &::after {
    top: 24px;
    left: 237px;
  }
}

@media (min-width: $tablet-width) {
  .country {
    background-color: #edeff6;
    position: relative;
    z-index: 7;
    padding-bottom: 0px;
    margin-top: -32px;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      box-sizing: border-box;
      height: 60px;
      background-color: #192144;
      border-radius: 0 0 60px 60px;
    }
  }

  .country__wrapper {
    width: 688px;
    margin: 0 auto;
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    padding-top: 0;
  }

  .country__title {
    padding-left: 33px;
    padding-top: 96px;
    font-size: 45px;
    line-height: 45px;
    margin-bottom: 55px;
  }

  .country__title-description {
    padding-left: 35px;
    font-size: 28px;
    line-height: 33px;
    width: 345px;
    position: relative;
    margin-bottom: 107px;

    &::after {
      position: absolute;
      content: "";
      background-image: url("../img/flags-back@1x.png");
      width: 289px;
      height: 575px;
      top: -92px;
      left: 399px;
    }
  }

  .country__list {
    padding: 0;
    margin: 0;
    margin-bottom: 41px;
  }

  .country__item {
    flex-direction: row-reverse;
    margin-bottom: 9px;
    height: 140px;
    position: relative;
    z-index: 1;

    &:nth-child(1) {
      &::after {
        position: absolute;
        content: "";
        background-image: url("../img/flag-thailand.svg");
        background-size: 70px, 47px;
        width: 70px;
        height: 47px;
        top: 15px;
        left: 433px;
        z-index: 1;
      }

      &::before {
        position: absolute;
        content: "";

        @include wh(141px, 141px);
        background-color: #e2e5f2;
        top: -70px;
        left: -35px;
        border-radius: 20px;
        z-index: -1;
      }
    }

    &:nth-child(2) {
      &::after {
        position: absolute;
        content: "";
        background-image: url("../img/flag-france.svg");
        background-size: 70px, 47px;
        width: 70px;
        height: 47px;
        top: 16px;
        left: 433px;
        z-index: 1;
      }
    }

    &:nth-child(3) {
      &::after {
        position: absolute;
        content: "";
        background-image: url("../img/flag-czech.svg");
        background-size: 70px, 47px;
        width: 70px;
        height: 47px;
        top: 17px;
        left: 433px;
        z-index: 1;
      }
    }

    &:nth-child(1) .country__review {
      margin-top: 0;

      &::after {
        background: none;
      }
    }

    &:nth-child(2) .country__review {
      margin-top: 0;

      &::after {
        background: none;
      }
    }

    &:nth-child(3) .country__review {
      margin-top: 0;

      &::after {
        background: none;
      }
    }
  }

  .country__image {
    padding: 0;
    margin: 0;
    height: 140px;
    width: 186px;
    z-index: 1;
    position: relative;
    border-radius: 0 20px 20px 0;
    margin-left: -35px;
    margin-right: 35px;
  }

  .country__review {
    padding: 0;
    margin: 0;
    position: relative;
    z-index: -1;
    width: 467px;
    border-radius: 20px;
    margin-left: 35px;
    flex-direction: column;
  }

  .country__title-review {
    font-size: 24px;
    line-height: 24px;
    padding: 0;
    margin: 0;
    padding-top: 31px;
    padding-left: 30px;
    padding-bottom: 4px;
  }

  .country__text {
    margin: 0;
    padding: 0;
    padding-left: 30px;
    font-size: 20px;
    line-height: 18px;
    margin-bottom: 17px;
  }

  .country__counter {
    padding: 0;
    margin: 0;
    font-size: 20px;
    line-height: 30px;
    padding-left: 60px;

    &::before {
      top: 3px;
      left: 30px;
      content: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3C!-- Generator: Adobe Illustrator 21.0.0, SVG Export Plug-In . SVG Version: 6.00 Build 0) --%3E%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 20 20' height='20' width='20' style='enable-background:new 0 0 20 20;' xml:space='preserve'%3E%3Cg id='icons'%3E%3Cg id='user'%3E%3Cellipse fill='%23192144' cx='10' cy='6' rx='5' ry='6'/%3E%3Cpath fill='%23192144' d='M19.8,17.1c-0.9-1.8-2.6-3.3-4.8-4.2c-0.6-0.2-1.3-0.2-1.8,0.1c-1,0.6-2,0.9-3.2,0.9S7.8,13.6,6.8,13 c-0.5-0.2-1.2-0.3-1.8,0c-2.2,0.9-3.9,2.4-4.8,4.2C-0.5,18.5,0.6,20,2.1,20h15.8C19.4,20,20.5,18.5,19.8,17.1z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
    }
  }

  .country__button {
    margin-left: 35px;
    width: 618px;
    font-size: 24px;
    line-height: 24px;
    text-align: left;
    border-radius: 20px;

    span {
      padding-top: 18px;
      padding-bottom: 18px;
      padding-left: 34px;
    }

    &::after {
      left: 573px;
      top: 33px;
    }
  }
}

@media (min-width: $desktop-width) {
  .country {
    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      box-sizing: border-box;
      height: 60px;
      background-color: #192144;
      border-radius: 0 0 60px 60px;
    }
  }

  .country__wrapper {
    width: 1360px;
    margin: 0 auto;
    flex-direction: row;
    align-content: flex-end;
    flex-wrap: wrap;
    padding-bottom: 93px;
    padding-top: 58px;
  }

  .flex {
    order: 2;
    width: 600px;
    margin-left: auto;
  }

  .country__list {
    order: 1;
    padding-left: 56px;
    padding-top: 161px;
    width: 629px;
    margin-bottom: 0;
  }

  .country__title-review {
    padding-top: 25px;
  }

  .country__item:nth-child(2),
  .country__item:nth-child(3) {
    &::after {
      top: 24px;
      left: 373px;
    }
  }

  .country__item {
    &:nth-child(1) {
      position: relative;

      &::before {
        position: absolute;
        content: "";

        @include wh(141px, 141px);
        background-color: #e2e5f2;
        top: -70px;
        left: -35px;
        border-radius: 20px;
        z-index: -1;
      }

      &::after {
        top: 24px;
        left: 373px;
      }
    }
  }

  .country__image {
    width: 213px;
  }

  .country__title {
    padding-top: 130px;
    padding-left: 62px;
    position: relative;
    z-index: 2;
  }

  .country__title-description {
    padding-left: 62px;
    z-index: 1;
    position: relative;

    &::after {
      z-index: -1;
      top: -180px;
      left: 290px;
    }
  }

  .country__button {
    margin-left: auto;
    width: 274px;
    z-index: 1;
    justify-content: flex-end;
    order: 3;
    margin-top: -81px;
    margin-right: 262px;

    span {
      font-size: 24px;
      padding-right: 0;
    }

    &::after {
      top: 34px;
      left: 228px;
    }
  }
}
