.traveler__list {
  @include list-reset;
  margin-bottom: 20px;
}

.traveler__item {
  width: 270px;
  background-color: $white;
  margin: 0 auto;
  border-radius: 20px;
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  padding-bottom: 22px;
}

.traveler__item--demin {
  .traveler__wrapper {
    margin-top: -7px;
  }
}

.traveler__item--smolov {
  .traveler__wrapper {
    margin-top: -10px;
  }
}

.traveler__item--rogovaya {
  .traveler__wrapper {
    margin-top: -8px;
  }
}

.traveler__about {
  order: 1;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.traveler__image {
  border-radius: 50%;
  margin-top: 15px;
  margin-left: 18px;
  margin-right: 16px;
}

.traveler__name {
  @include font-parametrs(20px, #1d2e5b, 700, 20px);
  margin-top: 16px;
  width: 80px;
}

.traveler__tags {
  width: 320px;
  @include font-parametrs(15px, #444444, 400, 18px);
  width: 180px;
  margin-top: 10px;
  margin-left: 25px;
}

.traveler__like {
  order: 2;
  margin-top: -73px;
  margin-left: 105px;
  span {
    @include font-parametrs(14px, #161c35, 700, 21px);
    text-transform: uppercase;
  }
}

