    background-color: #f9eaea;
    border-radius: 50%;
  }

  &::after {
    content: "";
    position: absolute;
    @include wh(31px, 31px);
    background-color: #f9eaea;
    border-radius: 50%;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3C!-- Generator: Adobe Illustrator 21.0.0, SVG Export Plug-In . SVG Version: 6.00 Build 0) --%3E%3Csvg version='1.1' id='heart' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' fill='%23f02323' fill-opacity='0.3' x='0px' y='0px'%0AviewBox='0 0 13 11' style='enable-background:new 0 0 13 11;' xml:space='preserve'%3E%3Cpath d='M12,1c-1.3-1.3-3.4-1.3-4.7,0L6.5,1.7L5.7,1C4.4-0.3,2.3-0.3,1,1c-1.3,1.3-1.3,3.4,0,4.6l0.8,0.8L6.5,11l4.7-4.6L12,5.6%0AC13.3,4.3,13.3,2.2,12,1z'/%3E%3C/svg%3E%0A");
