.traveler__like-input:checked + .traveler__like-label::before {
  display: none;
}

.traveler__like-input:checked + .traveler__like-label::after {
  display: block;
}

.wish-country {
  order: 3;
  width: 100%;
  margin-top: -2px;
}

.wish-country__list {
  @include list-reset;
  margin-left: 26px;
  width: 100%;
}

.wish-country__item {
  display: flex;
  align-content: center;
  margin-bottom: 10px;
}

.wish-country__title {
  @include font-parametrs(15px, #444444, 400, 18px);
  text-transform: lowercase;
  margin-left: 26px;
  margin-bottom: 18px;
}

.wish-country__name {
  @include font-parametrs(15px, #1d2e5b, 500, 18px);
  text-transform: uppercase;
}

.wish-country__image {
  @include wh(26px, 18px);
  margin-right: 13px;
}

.traveler__wrapper {
  order: 4;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: -13px;
  margin-left: 25px;
  margin-right: 17px;
  margin-bottom: 10px;
}

.transport__title,
.level__title {
  @include font-parametrs(15px, #444444, 400, 18px);
  text-transform: lowercase;
}

.transport__list {
  @include list-reset;
  display: flex;
}

.level__image {
  margin-top: -8px;
  margin-right: 3px;
  font-family: $main-font;
}

.level__image-tablet {
  display: none;
}

.transport__item--plane {
  position: relative;
  &::before {
    top: 0;
    left: 0;

    position: absolute;
    content: "";
    background-image: url(/img/plane.svg);
    background-size: 14px, 16px;
    @include wh(33px, 32px);
    background-color: #edeff6;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 1;
  }
}

.transport__item--bus {
  position: relative;
  &::before {
    top: 0;
    left: 33px;

    position: absolute;
    content: "";
    background-image: url(/img/bus.svg);
    background-size: 13px, 16px;
    @include wh(33px, 32px);
    background-color: #edeff6;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 1;
  }
}

.transport__item--bike {
  position: relative;
  &::before {
    top: 0;
    left: 66px;

    position: absolute;
    content: "";
    background-image: url(/img/bike.svg);
    background-size: 19px, 17px;
    @include wh(33px, 32px);
    background-color: #edeff6;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 1;
  }
}

.transport__item--run {
  position: relative;
  &::before {
    top: 0;
    left: 99px;

    position: absolute;
    content: "";
    background-image: url(/img/run.svg);
    background-size: 15px, 17px;
    @include wh(33px, 32px);
    background-color: #edeff6;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 1;
  }
}

.traveler__button {
  display: block;
  order: 5;
  width: 230px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  border-radius: 20px;
  background-color: #ffd74b;
  @include font-parametrs(17px, #161c35, 700, 30px);
  text-transform: uppercase;
  text-decoration: none;
}

.traveler__showbtn {
  width: 120px;
  @include font-parametrs(17px, #1d2e5b, 700, 30px);
  text-transform: uppercase;
  margin: 0 auto;
  display: block;
  margin-left: 118px;
  text-decoration: none;
  margin-bottom: 20px;
}


.pagination {
  position: relative;
  padding-bottom: 85px;
}

.pagination__list {
  display: none;
}

.pagination-button {
  @include list-reset;
  display: flex;
  width: 270px;
  margin: 0 auto;
}

.pagination-button {
  width: 50%;
  height: 50px;
  background-color: $white;
  border: none;
}

.pagination-button--back {
  border-radius: 20px 0 0 20px;
  width: 50%;
  position: relative;
  &::after {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 10px 10px 0;
    border-color: transparent #192144 transparent transparent;
    top: 15px;
    left: 63px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 1px;
    height: 50px;
    background: #edeff6;
    top: 0;
    left: 135px;
    z-index: 99;
  }
}

.pagination-button--next {
  border-radius: 0 20px 20px 0;
  position: relative;
  &::after {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent #192144;
    top: 15px;
    left: 63px;
  }
}

