.filter {
  background-color: #e2e5f2;
  position: relative;
  z-index: 98;
  display: flex;
  flex-wrap: wrap;
  margin-top: -30px;
  border-radius: 0 0 20px 20px;
  margin-bottom: 24px;
}

.filter__wrapper {
  padding-top: 17px;
  width: 84.375%;
  margin: 0 auto;
  padding-bottom: 0;
  margin-bottom: -40px;
}

.filter__title {
  @include font-parametrs(16px, #192144, 700, 22px);
  padding-left: 52px;
  padding-top: 34px;
  width: 195px;
  text-transform: uppercase;
  margin-right: auto;
  position: relative;
  &::before {
    position: absolute;
    content: "";
    background-image: url(/img/filter.svg);
    width: 15px;
    height: 10px;
    top: 39px;
    left: 25px;
  }
}

.filter__wrapper .filter__button {
  order: 1;
  position: relative;
  border: none;

  &::before {
    position: absolute;
    content: "";
    background-image: url(/img/dots.svg);
    top: -59px;
    left: 249px;
    width: 17px;
    height: 3px;
    background-repeat: no-repeat;
  }
}

.filter__wrapper--active .filter__button {
  &::before {
    position: absolute;
    content: "✕";
    top: -60px;
    left: 238px;
    width: 0;
    height: 0;
    font-size: 18px;
    border-radius: 0%;
    box-shadow: none;
  }
}

.filter__list {
  order: 3;
  @include list-reset;
  margin-top: 0px;
  margin-left: 25px;
  margin-bottom: 17px;
}

.filter__item {
  margin-bottom: 5px;
}

.filter__item a {
  @include font-parametrs(16px, #192144, 700, 22px);
  text-transform: uppercase;
  text-decoration: none;
  opacity: 0.5;
}

.filter__item--current a {
  opacity: 1;
}

.filter__country-list {
  order: 5;
  @include list-reset;
  margin-left: 26px;
  align-items: flex-start;
  margin-bottom: 24px;
}

.filter__country-item a {
  @include font-parametrs(16px, #444444, 400, 22px);
  text-decoration: none;
}

.filter__country-button {
  order: 6;
  width: 100%;
  background-color: #d6daed;
  border: none;
  margin-bottom: 27px;
  border-radius: 20px;
  span {
    text-transform: uppercase;
    @include font-parametrs(17px, #161c35, 700, 29px);
    display: flex;
    justify-content: center;
    padding-top: 6px;
    padding-bottom: 6px;
  }
}

.filter__country-list:not(:first-of-type) {
  display: none;
}

//Переопределение стилей при закрытом фильтре

.filter__wrapper {
  .filter__list,
  .alphabet,
  .filter__country-list,
  .filter__country-button {
    display: none;
  }
}

//Переопредление стилей при открытом фильтре

.filter__wrapper--active {
  .filter__list,
  .filter__country-list,
  .filter__country-button {
    display: block;
  }
}

.filter__wrapper--active .alphabet {
  display: flex;
}

.filter__wrapper--active .filter__country-list:not(:first-of-type) {
  display: none;
}



@media (min-width: $tablet-width) {
  .filter {
    margin-top: -50px;
    border-radius: 0 0 60px 60px;
    padding-bottom: 5px;
    margin-bottom: 45px;
  }

  .filter__title {
    display: none;
  }

  .filter__wrapper {
    width: 688px;
    padding-top: 110px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
  }

  .filter__wrapper .filter__button {
    order: 2;
    position: relative;
    border: none;
    @include wh(12px, 12px);
    background: transparent;
    &:hover {
      cursor: pointer;
    }

    &::after {
      top: -1px;
      left: 17px;
      width: 176px;
      position: absolute;
      content: "Показать все";
      transition: 0.5s;
      text-transform: uppercase;
      @include font-parametrs (20px, #192144, 700, 24px);
    }

    &::before {
      position: absolute;
      content: "";
      background-image: url(/img/dots.svg);
      top: 6px;
      left: 190px;
      width: 27px;
      height: 5px;
      background-repeat: no-repeat;
    }
  }

  .filter__wrapper--active .filter__button {
    display: block;
    margin-top: 0px;
    position: relative;
    order: 2;
    margin-right: 0;
    width: 175px;
    &::after {
      top: 0;
      left: 0;
      width: 176px;
      position: absolute;
      content: "Свернуть";
      transition: 0.5s;
      text-transform: uppercase;
      @include font-parametrs (20px, #192144, 700, 24px);
    }

    &::before {
      position: absolute;
      content: "✕";
      top: 2px;
      left: 193px;
      width: 0;
      height: 0;
      font-size: 28px;
      border-radius: 0%;
      box-shadow: none;
    }
  }

  .filter__wrapper .filter__list {
    order: 1;
    display: flex;
    width: 365px;
    justify-content: space-between;
    margin-right: 38px;
    margin-left: 33px;
    margin-bottom: 45px;
    a {
      font-size: 20px;
    }
  }

  .filter__country-list {
    padding-top: 8px;
  }

  .filter__country-item a {
    font-size: 20px;
    line-height: 30px;
  }

  .filter__country-button {
    width: 618px;
    height: 100px;
    display: flex;
    padding-left: 42px;
    padding-top: 3px;
    justify-content: center;
    align-items: center;
    margin-left: 35px;
    span {
      font-size: 20px;
    }
  }

  //Переопределение стилей при закрытом фильтре

  .filter__wrapper {
    .alphabet,
    .filter__country-list,
    .filter__country-button {
      display: none;
  }
}

//Переопредление стилей при открытом фильтре

  .filter__wrapper--active {
    .filter__country-button,
    .filter__list,
    .alphabet {
      display: flex;
    }
  }

  .filter__wrapper--active .filter__country-list {
    display: block;
  }

  .filter__wrapper--active .filter__country-wrapper {
    order: 3;
    display: flex;
  }

  .filter__wrapper--active .filter__country-list:not(:first-of-type) {
    display: none;
  }
}

